* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.main-box {
  width: 90%;
  padding: 20px;
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
h2 {
  width: 100%;
  margin: 20px 0;
  text-align: center;
  text-transform: uppercase;
  color: rgb(85, 209, 95);
  font-size: 32px;
}
.main-table,
.no-task {
  width: 250px;
  margin: 10px;
}
.task-heading,
.no-task {
  font-weight: 700;
  text-align: center;
  text-transform: capitalize;
  padding: 10px 0;
  color: rgb(91, 99, 117);
  background-color: rgb(169, 248, 251);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.main-table div:last-child {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.task-row {
  text-transform: capitalize;
  width: 100%;
  height: 45px;
  padding: 10px 15px 10px 20px;
  margin: 2px 0;
  color: rgb(75, 93, 94);
  background-color: rgb(230, 247, 255);
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}
.task-name {
  width: 60%;
}
.task-btn-div {
  width: 25%;
  display: flex;
  justify-content: flex-end;
}
.task-row button {
  width: 20px;
  height: 20px;
  margin: 0 5px;
  border-radius: 50%;
  background-color: rgb(230, 247, 255);
  border: none;
}
.task-row button:hover {
  box-shadow: 4px 4px rgb(255, 255, 112);
}
.task-row img {
  width: 100%;
  object-fit: cover;
}
.no-task {
  height: 60px;
  border-radius: 10px;
  background-color: rgb(169, 251, 176);
  display: flex;
  justify-content: center;
  align-items: center;
}
.source {
  font-size: 8px;
  color: rgb(201, 203, 204);
  width: 100%;
  text-align: center;
  margin: 40px;
}
