.add-task-table {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
small {
  color: red;
  width: 50%;
  margin: 0 5px;
  font-size: 12px;
}
.add-task-form {
  width: 100%;
  margin: 10px 0;
  display: flex;
  flex-flow: row wrap;
}
.add-task-table .task-heading {
  width: 100%;
}
input {
  width: 100%;
  height: 35px;
  padding: 5px;
  margin: 5px 0 2px 0;
  text-align: center;
  font-size: 16px;
  color: rgb(75, 93, 94);
  background-color: rgb(255, 255, 234);
  border: 1px solid rgb(121, 230, 179);
}
input:focus {
  outline: none;
  border: 3px solid rgb(121, 230, 179);
}
.for-button {
  width: 40%;
  text-align: right;
  margin-top: 5px;
}
.add-task-btn {
  padding: 5px 10px;
  color: white;
  background-color: rgb(42, 218, 135);
  border-radius: 5px;
  border: none;
}
.add-task-btn:hover {
  background-color: rgb(4, 180, 98);
}
.add-task-btn:active {
  background-color: rgb(0, 116, 62);
}
.display-btn {
  width: 30px;
  height: 30px;
  background-color: none;
  border: none;
  overflow: hidden;
  border-radius: 50%;
  margin: 5px;
}
.display-btn:hover {
  box-shadow: 4px 4px rgb(255, 255, 112);
}
.add-task-box {
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
  align-items: flex-end;
}
.display-btn img {
  width: 100%;
  object-fit: cover;
}
